<script>
export default {
  name: 'Home',
  data () {
    return {
      height2: 0,
      menuList: [
        {id: "Home",page:1,len:2},
        {id: "Product",page:1,len:13},
        {id: "App",len:1},
        {id: "Contact",len:1},
      ],
      currentMenu: {},
      scrollWaiting: false,
    }
  },
  mounted(){
    this.menuChange(this.menuList[0]);
    let that = this;
    var scrollFunc=function(e){  
      e=e || window.event;  
      if(e.wheelDelta){//IE/Opera/Chrome  
        //自定义事件：编写具体的实现逻辑  
        that.mouseScroll(e.wheelDelta);  
      }else if(e.detail){//Firefox  
        //自定义事件：编写具体的实现逻辑  
        that.mouseScroll(e.detail);  
      }  
    }
    /*监听鼠标滚动事件  
    * 1.火狐的是：DOMMouseScroll;  
    * 2.IE/Opera/Chrome：
    * 直接添加事件*/  
    if(document.addEventListener){  
      document.addEventListener('DOMMouseScroll',scrollFunc,false);  
    }//W3C  
    window.onmousewheel=document.onmousewheel=scrollFunc;//IE/Opera/Chrome
  },
  methods: {
    menuChange(data){
      this.currentMenu = data;
    },
    mouseScroll(direction){
      if(this.scrollWaiting)return;
      let that = this;
      that.scrollWaiting = true;
      if(that.currentMenu.id=='Home'||that.currentMenu.id=='Product'){
        if(direction<0&&that.currentMenu.len&&that.currentMenu.page<that.currentMenu.len){//向下滚动
          that.currentMenu.page++;
        }else if(direction>0&&that.currentMenu.len&&that.currentMenu.page>1){
          that.currentMenu.page--;
        }
      }
      setTimeout(() => {
        that.scrollWaiting = false;
      }, 500);
    }
  }
}
</script>


