<template>
  <div class="contentBox">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  mounted  () {
  	let that = this;
  	setTimeout(function(){
  		that.setMaxWidth();
  	},600);
    window.removeEventListener("resize",that.setMaxWidth);
    window.addEventListener('resize', that.setMaxWidth, false);
  },
  methods: {
  	setMaxWidth(){
      console.log('----------------')
  		let box = document.getElementsByClassName("contentBody")[0];
  		if(box){
        let width = window.innerWidth;
        if(width<=1200){
          width = 1200;
        }
        console.log(width);
        box.style.height = parseInt(width*9/16)+"px";
      }
  	}
  }
}
</script>

<style>
  html,body{
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
  }
  .contentBox {
    min-width: 1200px;
    position: relative;
    width: 100%;
    height: 100%;
    /*overflow-x: auto;*/
    overflow: hidden;
    margin: 0;
    padding: 0;
    color: #000;
    background-color: #000;
  }
</style>



